.form {
  display: flex;
  align-items: center;
  margin: 20px auto;
  width: 400px;
}
.input {
  font-weight: 400;
  border-radius: 5px;
  border: 2px solid coral;
  outline: none;
  color: #212121;
  width: 250px;
}
.button {
  border-radius: 5px;
  background-color: coral;
  outline: none;
  border: 1px solid coral;
  height: 25px;
}
