.nav {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 2;
  display: flex;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 20px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #fff;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
.link {
  color: #212121;
  font-size: 22px;
  font-weight: 500;
  text-decoration: none;
}
.link:not(:last-child) {
  margin-right: 22px;
}
.activeLink {
  color: coral;
}
